import f38 from "./photo/f-38.jpg";
import f39 from "./photo/f-39.jpg";
import { ReactComponent as Connecting } from './connecting.svg';
import { withGetScreen } from 'react-getscreen'



class App extends Component {
  constructor() {
    super();
    this.state = {
      image: [f1,f2,f3,f4,f5,f6,f7,f8,f9,f10,f11,f12,f13,f14,f15,f16,f17,f18,f19,f20,f21,f22,f23,f24,f25,f26,f27,f28,f29,f30,f31,f32,f33,f34,f35,f36,f37,f38,f39],
      imageDisplay:f4
    };
  }





  mouseStop = () => {
    document.getElementById("video1").className = "mouseNotHoverTop"
    document.getElementById("logo").className = "logoView";
    this.setState({
      swap: !this.state.swap,
      visable: false

    })

  }



  mouseMove = () => {
    document.getElementById("video1").className = "mouseHover";
    document.getElementById("logo").className = "logoHide";
    this.setState({ visable: true })

    clearTimeout(this.myTimeOut);

    this.myTimeOut = setTimeout(this.mouseStop, 200);

  }

  startMobile = () => {
    document.getElementById("video1").className = "mouseHover2";
    document.getElementById("logo").className = "logoHide";
    this.setState({ visable: true })
  }

  endMobile = () => {
    var x = Math.floor(Math.random() * Math.floor(38));
    document.getElementById("video1").className = "mouseNotHoverTop"
    document.getElementById("logo").className = "logoView";
    this.setState({
      swap: !this.state.swap,
      visable: false,
      imageDisplay: this.state.image[x]
    })

  }

  render() {

    ////// Moblie version

    if (this.props.isMobile() || this.props.isTablet()) {
      return (
        <div id="theBody" onTouchEnd={this.endMobile} onTouchStart={this.startMobile} >




          {this.state.visable && <div className="fixeDiv">
            <div className="anim3" onClick={this.mail} >

              {!this.state.swap && <  Connecting />}

            </div>

          </div>}





          <div id="logo" className="logoView" >
            <div className="anim11"><  Anim1 />
            </div>

          </div>

          <div className="colorBackground">
            <img id="video1" src={this.state.imageDisplay} className="mouseNotHoverTop" alt="" />
          </div>

        </div>
      )

    }


    ////// destop version
    else {





      return (
        <div id="theBody" onMouseMove={this.mouseMove}>




          {this.state.visable && <div className="fixeDiv">
            <div className="anim2" onClick={this.mail} >

              {!this.state.swap && <  Connecting />}

            </div>

          </div>}

          <div id="logo" className="logoView" >
            <div className="anim1"><  Anim1 />
            </div>

          </div>

          <div className="colorBackground">
            <video id="video1"
              className="mouseNotHoverTop"
              ref="vidRef"
              loop
              muted
              autoPlay
              controls >
              <source src={loop} type="video/mp4" />
              <source src={loopWebm} type="video/webm" />
            </video>
          </div>
        </div>
      )

    }
  }
}

export default withGetScreen(App);

